<template>
  <div class="myrecord">
    <table class="table table-striped">
      <thead>
        <tr>
          <th>Home Team</th>
          <th>State</th>
          <th class="col-xs-3">Profits</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in myList" :key="item">
          <td>{{item.teamName}}</td>
          <td>{{item.message}}</td>
          <td>
            <input type="number" class="form-control" v-model="item.profit" placeholder="profit" />
          </td>
        </tr>
      </tbody>
    </table>
    <div>Profits Sum:
      <span v-bind:class="{red:msg>=0,green:msg<0}">{{msg}}</span>
    </div>
    <textarea class="log" v-show="showLog" v-model="editModel">
    </textarea>
    <div class="btn-group btn-group-lg">
      <button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        Log
        <span class="caret"></span>
      </button>
      <ul class="dropdown-menu">
        <li>
          <a v-on:click="toggleLog">{{showLog ? 'HideLog':'ShowLog'}}</a>
        </li>
        <li>
          <a v-show="showLog" v-on:click="saveLog">SaveLog</a>
        </li>
      </ul>
    </div>
    <div class="btn-group btn-group-lg">
      <button type="button" class="btn btn-default" v-on:click="save">Save</button>
    </div>
    <div class="btn-group btn-group-lg">
      <button type="button" class="btn btn-default" v-on:click="showProfits">Profits</button>
    </div>
  </div>
</template>

<script>
import Store from '../services/store.js';
export default {
  name: 'myrecord',
  data() {
    return {
      msg: 0,
      showLog: false,
      myList: (Store.fetch().length > 0 ? Store.fetch() : []),
      editModel: []
    };
  },
  components: {
    Store
  },
  methods: {
    toggleLog: function () {
      this.showLog = !this.showLog;
      this.editModel = JSON.stringify(this.myList);
    },
    save: function () {
      Store.save(this.myList);
    },
    showProfits: function () {
      let sum = 0;
      this.myList.forEach((v) => {
        if (v.profit) {
          sum += parseFloat(v.profit);
        }
      });
      this.msg = sum.toFixed(1);
    },
    saveLog: function () {
      Store.save(JSON.parse(this.editModel));
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.myrecord {
  overflow-y: auto;
  padding-bottom: 92px;
}

tr,
th,
tbody {
  text-align: center;
}









/*ul {
  list-style-type: none;
  max-height: 500px;
  padding: 0;
  overflow-y: auto;
}*/


/*li {
  position: relative;
  border-left: 10px solid red;
  border-top: 1px solid;
  border-bottom: 1px solid;
  height: 40px;
  line-height: 40px;
}*/


/*li:nth-child(2n+1) {
  border-left: 10px solid #7979c7;
}*/

a {
  color: #42b983;
}

.log {
  width: 80%;
  text-align: center;
  background-color: #C7EDCC;
  margin: 10px auto;
  padding: 10px 20px;
  border-radius: 4px;
  border-radius: 4px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.5);
}

.center-btn {
  background: firebrick;
  width: 200px;
  height: 48px;
  border-radius: 4px;
  font-size: 1.6rem;
  margin: 10px;
}

.list-key {
  font-weight: bold;
}

li input {
  position: absolute;
  top: 0px;
  right: 0px;
  height: 30px;
  width: 40px;
  text-align: center;
  font-size: 1.2rem;
  border-radius: 50%;
  margin: 2px 1px;
}

span {
  font-size: 2.4rem;
  font-weight: bold;
}

.red {
  color: red;
}

.green {
  color: green;
}
</style>



